<template>
  <div class="register container">
    <h1>Register</h1>

    <form class="mt-2" @submit.prevent="registerUser">
      <input type="text" class="form-control my-2" v-model="user.name" placeholder="User">
      <input type="text" class="form-control my-2" v-model="user.email" placeholder="Email">
      <input type="text" class="form-control my-2" v-model="user.password" placeholder="Password">
      <b-button variant="primary" class="btn-block" type="submit">Register</b-button>
    </form>
  </div>
</template>

<script>
import {mapState} from 'vuex'

export default {
  name: 'Register',
  data() {
    return {
      user: {},
      message: '',
      dismissSecs: 5,
      dismissCountDown: 0,
    }
  },
  methods: {
    registerUser() {
      let config = {
        headers: {
          token: this.token
        }
      }
      this.axios.post('/users', this.user, config)
        .then(response => {
          console.log(response.data)
          this.$router.push({name:'Login'})
        })
        .catch(errors  => console.log(errors.response.data))
    },
  },
  computed: {
    ...mapState(['token'])
  }
}
</script>